/* header */
/* simulator */
@media (min-width: 1300) {
  body {
    min-width: 1100px;
  }
}
body {
  margin: 0;
  color: #333;
  font-size: 14px;
  overflow-x: auto;
  background-color: #fff;
  -webkit-font-smoothing: antialiased;
  font-family: PingFang SC, "Helvetica Neue", Arial, sans-serif;
}
p {
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
}
ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
}
.panda-doc-row {
  width: 100%;
}
@media (min-width: 1440px) {
  .panda-doc-row {
    width: 1440px;
    margin: 0 auto;
  }
}
.panda-doc-demo-block__title {
  margin: 0;
  font-weight: normal;
  font-size: 14px;
  color: rgba(69, 90, 100, 0.6);
  padding: 35px 15px 15px;
}
.panda-doc-demo-block:first-of-type .panda-doc-demo-block__title {
  padding-top: 20px;
}
.panda-doc-demo-block.has-padding > div {
  padding: 0 15px;
}
.side-nav {
  width: 100%;
  box-sizing: border-box;
  padding: 60px 15px 20px;
}
.side-nav .zanui-title,
.side-nav .zanui-desc {
  text-align: center;
  font-weight: normal;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.side-nav .zanui-title {
  margin: 0 0 15px;
}
.side-nav .zanui-title img,
.side-nav .zanui-title span {
  display: inline-block;
  vertical-align: middle;
}
.side-nav .zanui-title img {
  width: 36px;
}
.side-nav .zanui-title span {
  font-size: 40px;
  margin-left: 15px;
  font-family: "Dosis", "Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
}
.side-nav .zanui-desc {
  font-size: 14px;
  color: #455a64;
  margin: 0 0 60px;
}
body {
  background: #fafafa;
}
